<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="visible"
      :before-close="beforeClose"
    >
      <div class="textarea">
        <el-input
          type="textarea"
          placeholder=""
          v-model="LeaveWords"
          rows="10"
          @input="sensitiveWord()"
        ></el-input>
        <span>{{ LeaveWords.length }} </span> 
          
      </div>
      <div
        class="button"
        style="display: flex; justify-content: space-around; margin-top: 2vh"
      >
        <el-button @click="Reset()"></el-button>
        <el-button @click="AddApiLeaveWord()" type="primary"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      LeaveWords: "",
      visible: true,
    };
  },
  mounted() {},
  methods: {
    //
    beforeClose() {
      this.$router.back();
    },
    //
    AddApiLeaveWord() {
      if (this.LeaveWords == "") return;
      const id = localStorage.getItem("id");
      let time = new Date().toLocaleString();
      this.$axios
        .post(`/LeaveWords/AddLeaveWords`, {
          id: id,
          leavewords: this.LeaveWords,
          time: time,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.$notify.success({ message: "", title: "" })
              this.$bus.$emit("showAddLeaveWord")
              this.beforeClose()
            } else {
              this.$message.error({message:""})
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({message:"..."})
          }
        );
    },
    //
    sensitiveWord() {
      const reg =
        /|||||JB||||||||/gi;
      if (reg.test(this.LeaveWords)) {
        this.LeaveWords = this.LeaveWords.replace(reg, "*");
      }
    },
    //
    Reset() {
      this.LeaveWords = "";
      this.$refs.AddLeaveWords.focus();
    },
  },
};
</script>

<style lang="less" scoped>
span {
  float: right;
  font-size: 5px;
}
textarea::-webkit-scrollbar {
  display: none;
}
</style>