<template>
  <div>
    <div class="search">
      <input
        type="text"
        @keyup.enter="GetChooseShareUrlSearch()"
        v-model="search"
        placeholder=""
      />
    </div>
    <div class="scrollTop" v-if="scrollTopshow" @click="scrollTopNow()">
      <el-button
        circle
        type="primary"
        plain
        icon="el-icon-arrow-up"
      ></el-button>
    </div>
    <div class="classify">
      <select name="" @change="changeClassify()" ref="shareClassify">
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
      </select>
      <select
        name=""
        id=""
        @change="chaneClassifyTech()"
        ref="shareClassifyTech"
        v-if="showclassify"
      >
        <option value="JavaScript">JavaScript</option>
        <option value="Git">Git</option>
        <option value="Java">Java</option>
        <option value="Linux">Linux</option>
        <option value="Articles">Articles</option>
        <option value="C/C++">C/C++</option>
        <option value="phthon">phthon</option>
        <option value="php">php</option>
        <option value="MySQL">MySQL</option>
        <option value=""></option>
        <option value=""></option>
        <option value="" selected></option>
        <option value=""></option>
      </select>
    </div>
    <div class="add">
      ==>
      <button class="addbutton" @click="AddShareUrl()">{{ addText }}</button>
      <button class="sort" @click="sortUrl()"></button>
    </div>
    <div v-if="shareList.length != 0">
      <div class="box">
        <div class="item" v-for="item in shareList" :key="item.uuid">
          <div class="content" @click="OpneWindowNew(item.content)">
            <span class="name"></span>{{ item.content }}
          </div>
          <div class="title">
            <span class="name"></span>{{ item.title }}
          </div>
          <div class="user">
            <div class="major">{{ item.major }}</div>
            -
            <div class="username">{{ item.username }}</div>
            <div
              class="love"
              @click="loveAddClick(item.uuid, item.loveid)"
              :class="loveChange(item.loveid) ? 'lovechange' : ''"
            >
              {{ item.love }}
            </div>
          </div>
          <div class="button">
            <el-button
              icon="el-icon-star-off"
              @click="Enshrine(item.uuid)"
              type="warning"
            ></el-button>
            <el-button
              icon="el-icon-share"
              type="success"
              @click="ShareShare()"
            ></el-button>
            <el-popconfirm
              title=""
              @confirm="DeleteShareUrl(item.uuid)"
            >
              <el-button
                type="danger"
                icon="el-icon-delete
            "
                v-if="id == item.id"
                slot="reference"
              >
              </el-button>
            </el-popconfirm>
            <el-button
              type="primary"
              @click="copyShareUrl(item.content)"
              icon="el-icon-thumb"
            ></el-button>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <el-empty></el-empty>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addText: "",
      skip: 1,
      shareList: [],
      showclassify: false,
      search: "",
      scrollTopshow: false,
      id: "",
      time: "",
      sort: "",
      AndSrollBar: true,
    };
  },
  mounted() {
    this.$bus.$on("AddShareUrl", () => {
      this.skip = 1;
      this.GetShareUrl();
    });
    this.GetShareUrl();
    this.AddEnenUserScoll();
    this.id = localStorage.getItem("id");
  },
  methods: {
    //
    ShareShare() {
      this.$notify.info({
        message: "",
        title: "",
      });
    },
    //
    Enshrine(e) {
      this.$axios
        .get(`/ShareUrl/AddUserShareUrl?userId=${this.id}&shareUrlId=${e}`)
        .then((res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "" });
          } else {
            this.$message.error({ message: res.data.msg });
          }
        });
    },
    //
    sortUrl() {
      this.skip = 1;
      this.sort = "";
      this.GetShareUrl();
    },
    //
    loveAddClick(a, b) {
      const time = Date.now();
      if (this.oldTime != "") {
        if (this.oldTime - time < 2000) {
          this.$message.warning({ message: "" });
          return;
        }
      }
      const ifAdd = this.loveChange(b);
      if (ifAdd) {
        this.$axios
          .get(`/ShareUrl/DeleteShareUrlLoveid?uuid=${a}&id=${this.id}`)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.GetShareUrl();
                this.oldTime = Date.now();
                this.$message.info({ message: "( " });
                this.oldTime = "";
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "..." });
            }
          );
      } else {
        this.$axios
          .get(`/ShareUrl/AddShareUrlLoveid?uuid=${a}&id=${this.id}`)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.GetShareUrl();
                this.oldTime = Date.now();
                this.$message.success({ message: "()" });
                this.oldTime = "";
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "..." });
            }
          );
      }
    },
    //
    loveChange(e) {
      const reg = new RegExp(this.id, "g");
      if (reg.test(e)) {
        return true;
      } else {
        return false;
      }
    },
    //
    copyShareUrl(e) {
      if (navigator.clipboard && navigator.clipboard.writeText) {
        navigator.clipboard.writeText(e);
        this.$notify.success({ message: "", title: "" });
      } else {
        this.$message.warning({ message: "," });
      }
    },
    //
    scrollTopNow() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    //
    DeleteShareUrl(e) {
      this.$axios.get("/ShareUrl/DeleteShareUrl?uuid=" + e).then(
        (res) => {
          if (res.data.code == "1") {
            this.$message.success({ message: "" });
            this.skip = 1;
            console.log(this.shareList);
            if (this.shareList.length == 1) {
              this.sort = "";
              this.search = "";
              this.AndSrollBar = true;
            }
            this.GetShareUrl();
          }
        },
        (err) => {
          console.log(err);
          alert("...");
        }
      );
    },
    //
    GetChooseShareUrlSearch() {
      if (this.search == "" || this.search[0] == "\\") return;
      this.skip = 1;
      this.GetShareUrl();
    },
    //
    chaneClassifyTech() {
      const chaneClassifyTech = this.$refs.shareClassifyTech.value;
      this.$axios
        .get(
          "/ShareUrl/GetChooseShareUrlClassify?classifyTechnology=" +
            chaneClassifyTech
        )
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.shareList = res.data.data;
              if (res.data.data.length == 0) {
                this.skip = 0;
              }
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "..." });
          }
        );
    },
    //
    changeClassify() {
      const shareClassify = this.$refs.shareClassify.value;
      if (shareClassify == "") {
        this.showclassify = true;
      } else {
        this.showclassify = false;
      }
      if (shareClassify == "") {
        this.GetShareUrl();
      } else {
        this.$axios
          .get("/ShareUrl/GetChooseShareUrl?classify=" + shareClassify)
          .then(
            (res) => {
              if (res.data.code == "1") {
                this.shareList = res.data.data;
                if (res.data.data.length == 0) {
                  this.skip = 0;
                }
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "..." });
            }
          );
      }
    },
    //
    OpneWindowNew(e) {
      window.open(e);
    },
    //
    AddEnenUserScoll() {
      window.addEventListener("scroll", () => {
        const containerHeight = document.documentElement.clientHeight;
        const scrollContainer = document.documentElement;
        const scrollHeight = scrollContainer.scrollHeight;
        const scrollTop = scrollContainer.scrollTop;
        if (scrollTop >= 1000) {
          this.scrollTopshow = true;
        } else {
          this.scrollTopshow = false;
        }
        if (scrollTop + containerHeight >= scrollHeight - 1) {
          if (this.AndSrollBar) {
            this.skip++;
            this.$axios
              .get(
                `/ShareUrl/GetShareUrl?skip=${this.skip}&sort=${this.sort}&search=${this.search}`
              )
              .then(
                (res) => {
                  if (res.data.code == "1") {
                    if (res.data.data.records.length == 0) {
                      this.AndSrollBar = false;
                      this.$message.info({ message: "" });
                    } else {
                      const oldList = this.shareList;
                      this.shareList = [...oldList, ...res.data.data.records];
                    }
                  }
                },
                (err) => {
                  console.log(err);
                  this.$message.error({ message: "..." });
                }
              );
          }
        }
      });
    },
    //
    GetShareUrl() {
      this.$axios
        .get(
          `/ShareUrl/GetShareUrl?skip=${this.skip}&sort=${this.sort}&search=${this.search}`
        )
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.shareList = res.data.data.records;
            }
          },
          (err) => {
            console.log(err);
            this.$message.error({ message: "..." });
          }
        );
    },
    //
    AddShareUrl() {
      this.$router.push("/ShareUrl/AddShareUrl");
    },
  },
};
</script>

<style lang="less" scoped>
.classify {
  right: 2vw;
  position: absolute;
  height: 2vh;
}
.add {
  right: 2vw;
  position: absolute;
  margin-top: 10vh;
  .addbutton {
    color: white;
    background-color: green;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 1vw;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
      background-color: darkgreen;
    }
  }
  .sort {
    color: white;
    background-color: rgb(184, 7, 7);
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 1vw;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
      background-color: rgb(92, 7, 7);
    }
    &:focus {
      background-color: red;
    }
  }
}
.search {
  input {
    right: 2vw;
    position: absolute;
    margin-top: 20vh;
    border: none;
    outline: none;
    border-bottom: 1px solid darkolivegreen;
  }
}
.scrollTop {
  right: 2vw;
  position: fixed;
  bottom: 10vh;
  cursor: pointer;
}
.box {
  width: 90%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .item {
    border-bottom: 1px solid black;
    border-top: 1px solid black;
    width: 40%;
    margin: 1vh;
    height: auto;
    border-radius: 20px;
    overflow: auto;
    .name {
      color: red;
    }
    a {
      text-decoration: none;
    }
    .content {
      margin: 1vw;
      word-wrap: break-word;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      cursor: pointer;
    }
    .title {
      margin: 1vw;
      word-wrap: break-word;
      height: 20vh;
      overflow: auto;
    }
    .title::-webkit-scrollbar {
      display: none;
    }
    .user {
      margin-left: 1vw;
      display: flex;
      .love {
        margin-left: 1vw;
        color: gray;
        &.lovechange {
          color: red;
        }
      }
    }
    .button {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 2vh;
      button {
        padding: 5px 10px;
        margin-right: 1vw;
      }
    }
  }
  .item:hover {
    border-bottom: 1px solid darkblue;
    border-top: 1px solid darkred;
  }
  .item::-webkit-scrollbar {
    display: none;
  }
}
</style>