<template>
  <div>
    <el-dialog
      :visible.sync="visible"
      :before-close="beforeClose"
      title=""
    >
      <el-form label-width="auto">
        <el-form-item label="">
          <el-input v-model="content" style="80%">
            <el-button @click="clickCopy()" slot="append"></el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="title"
            @input="sensitiveWord()"
            type="textarea"
            rows="10"
          >
          </el-input>
          <span style="float: right">{{ title.length }}</span>
        </el-form-item>
        <el-form-item label="">
          <el-select
            name=""
            id=""
            v-model="classify"
            @change="classifyChoose()"
          >
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
          </el-select>
          <el-select
            name=""
            id=""
            v-model="classifyTechnology"
            v-if="technology"
          >
            <el-option value="JavaScript">JavaScript</el-option>
            <el-option value="Git">Git</el-option>
            <el-option value="Java">Java</el-option>
            <el-option value="Linux">Linux</el-option>
            <el-option value="Articles">Articles</el-option>
            <el-option value="C/C++">C/C++</el-option>
            <el-option value="phthon">phthon</el-option>
            <el-option value="php">php</el-option>
            <el-option value="MySQL">MySQL</el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value="" selected></el-option>
            <el-option value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="Reset()"></el-button>
        <el-button @click="AddShareUrl()" type="primary"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      title: "",
      content: "",
      technology: true,
      visible: true,
      classify: "",
      classifyTechnology: "",
    };
  },
  mounted() {},
  methods: {
    //
    beforeClose() {
      this.$router.back();
    },
    //
    AddShareUrl() {
      if (this.title == "" || this.content == "") {
        this.$message.warning({ message: "" });
        return;
      }
      const reg1 = /^https?:\/\//gi;
      const reg2 = /^www\./gi;
      if (!reg1.test(this.content) && !reg2.test(this.content)) {
        this.$message.warning({ message: "" });
        return;
      }
      const id = localStorage.getItem("id");
      if (this.technology == true) {
        this.$axios
          .post("/ShareUrl/AddShareUrl", {
            id: id,
            content: this.content,
            title: this.title,
            classify: this.classify,
            classifyTechnology: this.classifyTechnology,
          })
          .then((res) => {
            if (res.data.code == "1") {
              this.beforeClose();
              this.$message.success({ message: "" });
              this.$bus.$emit("AddShareUrl");
            }
          });
      } else {
        this.$axios
          .post("/ShareUrl/AddShareUrl", {
            id: id,
            content: this.content,
            title: this.title,
            classify: this.classify,
          })
          .then((res) => {
            if (res.data.code == "1") {
              this.beforeClose();
              this.$message.success({ message: "" });
              this.$bus.$emit("AddShareUrl");
            }
          });
      }
    },
    //
    classifyChoose() {
      if (this.classify == "") {
        this.technology = true;
      } else {
        this.technology = false;
      }
    },
    //
    Reset() {
      this.title = "";
      this.content = "";
    },
    //
    async clickCopy() {
      //
      navigator.permissions
        .query({
          name: "clipboard-read",
        })
        .then((result) => {
          if (result.state == "granted" || result.state == "prompt") {
            //
            navigator.clipboard.readText().then((text) => {
              this.content = text;
            });
          } else {
            this.$message.error({message:","})
          }
        });
      // const copy = await window.navigator.clipboard.readText();
      // this.content = copy;
    },
    //
    sensitiveWord() {
      const reg =
        /|||||JB||||||||||||/gi;
      if (reg.test(this.title)) {
        this.title = this.title.replace(reg, "*");
      }
    },
  },
};
</script>
  
  <style lang="less" scoped>
</style>