<template>
  <div>
    <el-dialog :visible="visible" title="" :before-close="beforeClose">
      <el-form :model="form" :rules="rules" ref="AddSaveMessageRef">
        <el-form-item label="" prop="message">
          <el-input
            @input="sensitiveWord"
            v-model="form.message"
            type="textarea"
            rows="10"
            show-word-limit
            maxlength="500"
          ></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: flex-end; margin-top: 2%">
        <el-button size="mini" type="" @click="beforeClose"></el-button>
        <el-button size="mini" type="primary" @click="validate()"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: true,
      form: {
        userid: localStorage.getItem("id"),
        message: "",
        toUserid: "",
      },
      rules: {
        message: [{ required: true, message: "" }],
      },
    };
  },
  mounted() {
    this.form.toUserid = this.$route.query.to_userid;
  },
  methods: {
    //
    sensitiveWord() {
      const reg =
        /|QQ|vx|||||money|||yuan||weixin|wx|||qq|QQ|V|WEIXIN|WX|weixinhao|/gi;
      if (reg.test(this.form.message)) {
        this.form.message = this.form.message.replace(reg, "*");
      }
    },
    //
    validate() {
      this.$refs.AddSaveMessageRef.validate((e) => {
        if (e) {
          this.$axios.post("/Ws/SaveMessage", this.form).then(
            (res) => {
              if (res.data.code == "1") {
                this.$message.success({ message: "" });
                this.beforeClose();
              } else {
                this.$message.error({ message: "" });
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "..." });
            }
          );
        }
      });
    },
    //
    beforeClose() {
      this.$router.back();
    },
  },
};
</script>

