<template>
  <div class="all">
    <quill-editor
      style="width: 90%"
      class="editor"
      v-model="content"
    ></quill-editor>
    <el-button
      type="primary"
      @click="AddBlogNew()"
      style="position: fixed; right: 0; top: 0; margin: 1%"
      ></el-button
    >
    <el-button
      type="success"
      @click="ToImage()"
      style="position: fixed; right: 0; top: 20%; margin: 1%"
      ></el-button
    >
    <p style="position: fixed; right: 0; top: 40%; margin: 1%;font-size:10px;color:red">100M</p>

    <router-view></router-view>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";

export default {
  components: {
    quillEditor,
  },
  data() {
    return {
      content: "",
    };
  },
  methods: {
    //
    ToImage() {
      window.open("https://carbon.now.sh/");
    },
    //url
    insertImage() {
      const range = this.$refs.myQuillEditor.quill.getSelection();
      const url =
        "https://8.130.43.227:5551/images/242713c1-84a9-487a-bc9c-0915055b61a9_backone.jpg"; // URL

      if (range) {
        this.$refs.myQuillEditor.quill.insertEmbed(range.index, "image", url);
      }
    },
    //
    AddBlogNew() {
      if (this.content == "") {
        this.$message.warning({ message: "" });
        return;
      }
      // Blob
      const blob = new Blob([this.content], { type: "text/plain" });

      // Blob
      const contentSizeInBytes = blob.size;

      // MB
      const contentSizeInMB = contentSizeInBytes / (1024 * 1024);

      if (contentSizeInMB >= 100) {
        this.$message.warning({ message: "100MB" });
        return;
      }

      this.$router.push({
        path: "/BlogNew/AddBlogNew",
        query: {
          content: this.content,
        },
      });
    },
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
.editor {
  position: fixed;
  height: 90vh;
  margin-bottom: 10vh;
}
</style>
