<template>
  <div></div>
</template>

<script>
import Vue from "vue";
export default {
  data() {
    return {
      id: "",
      WebSocketToken: "!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj!@#$%^yjj",
    };
  },
  mounted() {
    this.$bus.$on("openWebSocket", () => {
      this.IfLogin();
    });
    this.IfLogin();
  },
  methods: {
    //websocket
    IfLogin() {
      if (
        this.$route.path != "/" &&
        this.$route.path != "/AssociationWindowSign"
      ) {
        if (localStorage.getItem("websocket") == null) {
          this.OpenWebSocket();
        }
      }
    },
    //websocket
    OpenWebSocket() {
      this.id = localStorage.getItem("id");
      let websocket = null;
      if ("WebSocket" in window) {
        websocket = new WebSocket("wss://8.130.43.227:8085/ws/" + this.id);
      } else {
        this.$message.error({ message: "WebSocket" });
      }
      websocket.onopen = () => {
        this.$axios.get("/User/LogInUserOne?id=" + this.id).then();
        console.log("");
      };
      websocket.onmessage = (e) => {
        //
        if (e.data.substring(0, 49) == this.WebSocketToken + "") {
          this.$confirm("", "")
            .then(() => {
              const message = {
                to: e.data.substring(49),
                content: this.WebSocketToken + "",
              };
              this.$websocket.send(JSON.stringify(message));
              this.$router.push({
                path: "/ChatBugHelp/UserChat",
                query: {
                  chatId: e.data.substring(49),
                },
              });
            })
            .catch(() => {
              const message = {
                to: e.data.substring(49),
                content: this.WebSocketToken + "",
              };
              this.$websocket.send(JSON.stringify(message));
            });
        }

        //
        if (e.data.substring(0, 49) == this.WebSocketToken + "") {
          this.$bus.$emit("closeLodingChatBugHelp");
          this.$router.push({
            path: "/ChatBugHelp/UserChat",
            query: {
              chatId: e.data.substring(49),
            },
          });
        } else if (
          e.data.substring(0, 49) ==
          this.WebSocketToken + ""
        ) {
          this.$bus.$emit("closeLodingChatBugHelp");
          this.$message.warning({ message: "" });
        }

        //
        if (e.data.substring(0, 49) == this.WebSocketToken + "") {
          this.$bus.$emit("closeUserChatBus");
        } else {
          //
          this.$bus.$emit("UserChatBus", e.data);
        }

        //
        if (e.data.substring(0, 49) == this.WebSocketToken + "") {
          this.$bus.$emit("unWebSocketMessage");
        }

        //
        if (e.data.substring(0, 50) == this.WebSocketToken + "") {
          this.$alert("", "")
            .then(() => {})
            .catch(() => {});
        }
      };
      websocket.onclose = () => {
        console.log("");
      };
      websocket.onerror = () => {
        console.log("");
      };
      Vue.prototype.$websocket = websocket;
    },
  },
  beforeDestroy() {
    this.$websocket.close();
  },
};
</script>

